<template>
  <div id="app" v-bind:class="bgClass">
    <header>
      <div id="title">Comunidad vórtice</div>
      <div id="subtitle">Únete a la aventura</div>
    </header>
    <div id="main">
      <div id="guilds">
        <div class="guild" v-for="guild of guilds" :key="guild.name">
          <div class="guild__name">{{ guild.name }}</div>
          <div class="guild__pg">{{ guild.pg }}M</div>
          <div class="guild__geonosis">{{ guild.geoLo }}<span class="guild__geonosis--star">*</span></div>
          <div class="guild__geonosis">{{ guild.geoLl }}<span class="guild__geonosis--star">*</span></div>
        </div>
      </div>
    </div>
    <footer>
      <div id="claim">Contacta en Discord con Temerario#1910 o dlopez#0941 para unirte.</div>
    </footer>
  </div>
</template>

<script>
import axios from 'axios'

const backgrounds = {
  bg1: '1',
  bg2: '2'
}

export default {
  name: 'app',
  components: {},
  data () {
    return { guilds: [] }
  },
  computed: {
    bgClass: function () {
      const random = parseInt(Math.random() * 100)
      const totalBackgrounds = Object.keys(backgrounds).length
      const randomBackground = (random % totalBackgrounds) + 1

      return { [`bg-${randomBackground}`]: true }
    }
  },
  async created () {
    const response = await axios.get('https://api.sheety.co/7a920bb3-df6b-4026-b1c8-68820bce555a')
    const guilds = response.data

    this.guilds = guilds
  }
}
</script>

